
import XHeader from 'vux/src/components/x-header/index.vue'

import Scroll from 'assembly/scroll/scroll'
import localStore from 'util/localStore'
export default {
data () {
return {
name: '******',
code: '**** **** **** ****'
}
},
methods: {
goBack () {
this.$router.go(-1)
},
copy (e) {
this.$vux.toast.text('复制成功')
},
onError (e) {
this.$vux.toast.text('复制失败，请重试')
},
_getDetail () {
this.$tupHttp(this, 'front/icbcAccount/getIcbcAccountByClient?clientUid=' + localStore.get('TUP_pUid'), 'GET').then(res => {
if (res.data.model) {
this.name = res.data.model.icbcName
this.code = res.data.model.icbcAccount.replace(/(.{4})/g, '$1 ')
} else {
let thiz = this
this.$vux.confirm.show({
title: '',
content: '暂未开放您的工行到账伴侣功能',
showCancelButton: false,
onConfirm () {
thiz.$router.go(-1)
}
})
}
})
}
},
created () {
this._getDetail()
},
components: {
Scroll,
XHeader
}
}



